/// Test Comment Sassdoc 2
@import '_mixins';

/// Test Comment Sassdoc 3
@mixin width($width){
	width: $width;
}

/// Color 1
$color1: orange;
/// Color 2
$color2: blue;
$color3: grey;

/// Test Comment Sassdoc 4
.test {
  @include width(880px);
  background-color: $color2;
  display: flex;
}

.testing {
  width: percentage(5/7);
}

.container {
  background: $color1;
}

.m-navigation {
  &:after {
  	color: blue; 
  	@media screen and (min-width: 320px) {
      content: '320px';
      display: block;
      width: 50%;
    }
    @media screen and (min-width: 767px) {
      content: '767px';
      display: inline-block;
      width: 100%;
    }
  }
  li {
    display: block;
 
    @media screen and (min-width: 767px) {
      float: left;
    }
  }
}



/// Horizontally center a block element
///
/// @example scss - Basic Usage Sass
///    .center-my-block-thingie {
///        @include margin-auto();
///    }
///
/// @example scss - Basic Usage CSS Output
///    .center-my-block-thingie {
///        margin-left: auto;
///        margin-right: auto;
///    }

@mixin margin-auto {
    margin-left: auto;
    margin-right: auto;
}